body {
    margin: 0;
    font-family: 'Outfit', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: 'Outfit', 'Courier New', monospace;
}
.App {
    width: 100vw;
    height: 100vh;
    background-color: hsl(212, 45%, 89%);
    display: flex;
    justify-content: center;
    align-items: center;
}

.qrcard {
    min-width: 18rem;
    min-height: 30rem;
    display: flex;
    flex: 1 1 auto;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: hsl(0, 0%, 100%);
    border-radius: 15px;
    font-size: 15px;
}

.App-logo {
    padding-top: 15px;
    max-width: 16rem;
    height: 100%;
    border-radius: 8px;
}

.text-container {
    max-width: 16rem;
    height: 100%;
    text-align: center;
    margin-top: -5px;
}

.title-container {
    font-weight: 700;
    max-width: 15rem;
}

.paragraph-container {
    font-weight: 400;
    max-width: 15rem;
}

.paragraph-container h3 {
    font-weight: 400;
    color: gray;
}
